<template>
	<view class="getlocation_con">
		<u-navbar title="选择地理位置" height="30px" back-text="返回" :custom-back="back">
		</u-navbar>
		<view class="content" style="margin-top: 120rpx;">
			<map style="width: 100%; height: 300px;" :latitude="latitude" :longitude="longitude" >
			</map>
			<button @click="get">点击</button>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				id: 0, // 使用 marker点击事件 需要填写id
				title: 'map',
				latitude: 0,
				// 29.813701,121.56052
				longitude: 0,
				// covers: [{
				// 	latitude: 39.909,
				// 	longitude: 116.39742,
				// 	iconPath: '../../static/images/location.png'
				// }, {
				// 	latitude: 39.90,
				// 	longitude: 116.39,
				// 	iconPath: '../../static/images/location.png'
				// }]
			}
		},
		methods: {
			back() {
				uni.navigateTo({
					url: '../writediary/writediary'
				});
			},
			get(){
				var vm = this
				uni.getLocation({
					type: 'wgs84',
					success: function(res) {
						vm.latitude = res.latitude
						vm.longitude = res.longitude
						console.log(res)
					},
					fail:function(err){
						console.log(err)
					}
				})
			}
		},
		onLoad: function() {
			// var vm = this
			// uni.getLocation({
			// 	type: 'wgs84',
			// 	success: function(res) {
			// 		vm.latitude = res.latitude
			// 		vm.longitude = res.longitude
			// 		console.log(res)
			// 	},
			// 	fail:function(err){
			// 		console.log(err)
			// 	}
			// })
		}
	}
</script>

